.card, .footer, .card-inverted, .navbar {
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5); }

.card, .footer, .card-inverted {
  border-radius: 5px;
  padding: 30px;
  display: block;
  margin-bottom: 5px; }

html {
  height: 100%;
  overflow-y: scroll;
  font-size: 100%; }

body {
  background-color: #F6F3F1;
  color: #111; }

body {
  line-height: 1.4; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Cutive';
  margin-bottom: 20px; }

h1 {
  font-size: 2.98598em; }
  @media (max-width: 768px) {
    h1 {
      font-size: 2.68739rem; } }
  @media (max-width: 576px) {
    h1 {
      font-size: 2.38879rem; } }

h2 {
  font-size: 2.48832em; }
  @media (max-width: 768px) {
    h2 {
      font-size: 2.23949rem; } }
  @media (max-width: 576px) {
    h2 {
      font-size: 1.99066rem; } }

h3 {
  font-size: 2.0736em; }
  @media (max-width: 768px) {
    h3 {
      font-size: 1.86624rem; } }
  @media (max-width: 576px) {
    h3 {
      font-size: 1.65888rem; } }

h4 {
  font-size: 1.728em; }
  @media (max-width: 768px) {
    h4 {
      font-size: 1.5552rem; } }
  @media (max-width: 576px) {
    h4 {
      font-size: 1.3824rem; } }

h5 {
  font-size: 1.44em; }
  @media (max-width: 768px) {
    h5 {
      font-size: 1.296rem; } }
  @media (max-width: 576px) {
    h5 {
      font-size: 1.152rem; } }

h6 {
  font-size: 1.2em; }
  @media (max-width: 768px) {
    h6 {
      font-size: 1.08rem; } }
  @media (max-width: 576px) {
    h6 {
      font-size: 1rem; } }

body {
  font-family: 'Lato';
  font-weight: 300;
  font-size: 1em; }
  @media (max-width: 768px) {
    body {
      font-size: 1rem; } }
  @media (max-width: 576px) {
    body {
      font-size: 1rem; } }

.page-content {
  min-height: calc(100vh - 50px - 35px); }

.footer {
  min-height: 50px; }

.card, .footer {
  background-color: #f8f7f7;
  width: 30%;
  margin-bottom: 10px; }

.card-inverted {
  background-color: #201e35;
  color: #f8f7f7;
  width: 30%; }

.card-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around; }

.footer {
  width: 100%;
  margin-bottom: 0px; }

.navbar-default, .navbar-brand {
  min-height: 70px; }
  .navbar-default .navbar-nav > li, .navbar-brand .navbar-nav > li {
    display: inline-block;
    height: 70px; }
    .navbar-default .navbar-nav > li a, .navbar-brand .navbar-nav > li a {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center; }
  @media (max-width: 768px) {
    .navbar-default, .navbar-brand {
      min-height: 50px; } }

.navbar {
  background-color: #f8f7f7; }
